import React from 'react';
import Firebase from './utils/firebase';
import hash from 'object-hash';

import Grid from './components/Grid';
import GameInfo from './components/GameInfo';
import PlayerForm from './components/PlayerForm';
import Admin from './components/Admin';
import Message from './components/Message';

import { generateNumbers, generateBlockNumbers } from './utils/numbers';

const styles = {
  container: {
    margin: 'auto',
    maxWidth: '500px',
    padding: '20px 10px',
  },
  newNumber: {
    display: 'block',
    textAlign: 'center',
    fontWeight: 'bold',
    fontSize: '3em',
    marginTop: 20,
  }
};

const GAME_EMPTY = {
  key: null,
  name: null,
  count: 0,
  players: [],
  numbers: [],
  paint: false,
  winner: null,
};

const PLAYER_EMPTY = {
  key: null,
  name: null,
  grid: [],
  numbers: [],
  finish: false,
};


// TODO config de game (auto / affichage new number / multi vainqueurs)
// TODO réinitialiser une partie
class App extends React.Component {
  constructor(props) {
    super(props);
    this.MESSAGES = {
      default: {
        confirm: 'Ok !',
        cancel: false,
        callback: this.removeMessage,
        timeout: false,
      },
      fullGrid: {
        content: <span>Il semblerait que tu aies un <b>BINGO !</b><br />Attention, si tu soumets une grille avec une erreur tu seras éliminé ! <br /><br />Veux-tu soumettre ta grille ou la corriger ?</span>,
        confirm: 'Soumettre !',
        cancel: 'Corriger',
        callback: this.submitGrid,
      },
      winner: {
        content: <span>Tu es le vainqueur de cette partie !</span>,
      },
      eliminated: {
        content: <span>Tu as des erreurs dans ta grille, tu as été éliminé :(</span>,
      },
      notExists: {
        content: <span>Cette partie n'existe pas, choisis en une autre ou lance la en choissant une clef !</span>,
        confirm: 'La lancer !',
        cancel: 'Une autre...',
        callback: this.maybeCreate,
      },
      newNumber: {
        content: data => <center>Nouveau nombre !<br /><span style={styles.newNumber}>{data.number}</span></center>,
        confirm: false,
        timeout: 1500,
      },
      newNumberAdmin: {
        content: data => <span style={styles.newNumber}>{data.number}</span>,
      },
      badHash: {
        content: <span>Ce mot de passe ne correspond pas à celui choisi lors de la création de la partie.</span>,
      },
      announceWinner: {
        content: data => <span>Cette partie est finie ! Le vainqueur est ...<br /><br /><center><b>{data.name}</b></center></span>,
      },
      displayWinner: {
        content: data => <span>Cette partie est finie ! Bravo au vainqueur ...<br /><br /><center><b>{data.name}</b></center></span>,
        confirm: 'Quitter la partie',
        callback: this.quitGame,
      },
    };
    this.ref = Firebase.database().ref('/');
    this.games = this.ref.child('games');
    this.players = this.ref.child('players');
    this.state = {
      admin: false,
      formOpened: 'join',
      game: GAME_EMPTY,
      player: PLAYER_EMPTY,
      message: null,
    };
  }

  // TODO si joueur quitte une partie, on supprime le player (?)
  // TODO cookie pour retenir key du player qui peut revenir si page fermée ou quitte
  quitGame = () => {
    this.removeMessage();

    if (this.state.game && this.state.game.key) {
      this.games.child(this.state.game.key).off();
      this.games.child(this.state.game.key).child('count').off();
      this.games.child(this.state.game.key).child('numbers').off();
      this.games.child(this.state.game.key).child('winner').off();
      this.players.orderByChild('game').equalTo(this.state.game.key).off();
      this.games.orderByChild('name').equalTo(this.state.game.name).off();
    }

    if (this.state.player && this.state.player.key) {
      this.players.child(this.state.player.key).off();
    }

    this.setState({
      admin: false,
      game: {
        ...GAME_EMPTY,
        name: this.state.game.name,
      },
      player: {
        ...PLAYER_EMPTY,
        name: this.state.player.name,
      },
    });
  };

  maybeCreate = (confirm) => {
    if (!confirm) {
      this.setState({
        game: GAME_EMPTY,
      });
    } else {
      this.changeFormOpened('create');
    }
    this.removeMessage();
  };

  submitGrid = (confirm) => {
    if (!confirm) {
      this.removeMessage();
    } else {
      this.players.child(this.state.player.key).child('finish').set(true);
      this.compareGrid();
    }
  };

  displayMessage = (message, data = {}) => {
    this.setState({
      message: {
        ...this.MESSAGES.default,
        ...this.MESSAGES[message],
        data,
      },
    });
  };

  compareGrid = () => {
    if (this.state.player.numbers.every(number => this.state.game.numbers.includes(number))) {
      this.games.child(this.state.game.key).child('winner').set(this.state.player.key);
    } else {
      this.displayMessage('eliminated');
    }
  };

  toggleNumber = (number) => {
    const index = this.state.player.numbers.indexOf(number);
    let numbers = this.state.player.numbers;

    if (index < 0) {
      numbers.push(number);
    } else {
      numbers.splice(index, 1);
    }

    if (numbers.length >= 24) {
      this.displayMessage('fullGrid');
    }

    this.players.child(this.state.player.key).child('numbers').set(numbers);
  };

  removeMessage = () => {
    this.setState({message: null});
  };

  changeGameName = (gameName) => {
    this.setState({
      game: {
        ...this.state.game,
        name: gameName,
      },
    });
  };

  changePlayerName = (playerName) => {
    this.setState({
      player: {
        ...this.state.player,
        name: playerName,
      },
    });
  };

  changeFormOpened = (formOpened) => {
    this.setState({formOpened});
  };

  searchGame = (gameName, callback) => {
    this.games.orderByChild('name').equalTo(gameName).once('value', snap => {
      const val = snap.val();

      if (val === null) {
        callback(null, null);
      } else {
        callback(Object.values(val)[0], Object.keys(val)[0]);
      }
    });
  };

  // TODO cookie avec key du player (ne pas recréer le player)
  // TODO choix : soit nouvelle grille (et on supprime l'ancien) / soit reprise)
  joinGame = () => {
    const gameName = this.state.game.name;
    const playerName = this.state.player.name;

    this.searchGame(gameName, (val, key) => {
      if (key === null) {
        this.displayMessage('notExists');
        return null;
      }

      // Vérifier s'il y a un vainqueur avant la création du joueur
      if (val.winner) {
        this.players.child(val.winner).once('value', snap => {
          this.displayMessage('announceWinner', {name: snap.val().name});
        });
        return;
      }

      const player = {
        ...PLAYER_EMPTY,
        name: playerName,
        grid: generateNumbers(true),
      };
      this.setState({
        player,
        game: {
          ...GAME_EMPTY,
          name: gameName,
          key: key,
        },
      }, () => {
        const ref = this.players.push({
          ...player,
          game: key,
        });
        const playerKey = ref.key;

        this.games.child(key).child('count').set((val.count || 0) + 1).then(() => {
          this.games.child(key).child('count').on('value', snap => {
            this.setState({
              game: {
                ...this.state.game,
                count: snap.val(),
              },
            });
          });
        });
        this.players.child(playerKey).on('value', snap => {
          this.setState({
            player: {
              ...this.state.player,
              key: playerKey,
              finish: (snap.val().finish || false),
              numbers: (snap.val().numbers || []),
            },
          });
        });
        this.games.child(this.state.game.key).child('numbers').on('value', snap => {
          const newNumbers = snap.val() || [];

          if (this.state.game.paint) {
            this.displayMessage('newNumber', {number: newNumbers[newNumbers.length - 1]});
          }

          this.setState({
            game: {
              ...this.state.game,
              numbers: newNumbers,
              paint: true,
            },
          });
        });
        this.games.child(this.state.game.key).child('winner').on('value', snap => {
          const winner = snap.val();
          this.setState({
            game: {
              ...this.state.game,
              winner,
            },
          });
          if (winner) {
            this.players.child(winner).once('value', snap => {
              this.displayMessage('displayWinner', {name: snap.val().name});
            });
          }
        });
        // TODO then store key in cookie
      });
    });
  };

  createGame = (gamePass) => {
    const gameHash = hash(gamePass);
    const gameName = this.state.game.name;

    this.searchGame(gameName, (val, key) => {
      if (key === null) {
        const ref = this.games.push({
          ...GAME_EMPTY,
          name: gameName,
          hash: gameHash,
        });
        key = ref.key;
      } else {
        if (val.hash !== gameHash) {
          this.displayMessage('badHash');
          return null;
        }
      }

      this.setState({
        admin: true,
        game: {
          ...GAME_EMPTY,
          name: gameName,
          key: key,
        },
      }, () => {
        this.games.child(key).child('numbers').on('value', snap => {
          this.setState({
            game: {
              ...this.state.game,
              numbers: snap.val() || [],
            }
          });
        });
        this.games.child(key).child('winner').on('value', snap => {
          const winner = snap.val();

          this.setState({
            game: {
              ...this.state.game,
              winner,
            }
          });

          if (winner) {
            this.players.child(winner).once('value', snap => {
              this.displayMessage('announceWinner', {name: snap.val().name});
            });
          }
        });
        this.players.orderByChild('game').equalTo(key).on('value', snap => {
          const players = snap.val() || {};
          this.setState({
            game: {
              ...this.state.game,
              players: Object.keys(players).map(playerKey => ({
                ...PLAYER_EMPTY,
                ...players[playerKey],
                key: playerKey,
              })),
            }
          })
        })
      });
    });
  };

  drawNumber = () => {
    if (this.state.game.numbers.length >= 75) {
      // TODO améliorer suite du process
      // Fin de la partie
      return null;
    }

    const numbers = generateBlockNumbers(1, 75, 75);
    let index = 0;
    while (this.state.game.numbers.includes(numbers[index])) {
      index++;
    }

    this.games.child(this.state.game.key).child('numbers').set([
      ...this.state.game.numbers,
      numbers[index],
    ]);

    this.displayMessage('newNumberAdmin', {number: numbers[index]});
  };

  render() {
    return (
      <div style={styles.container}>
        <GameInfo
          admin={this.state.admin}
          gameName={this.state.game.name}
          playerName={this.state.player.name}
          gridNumbers={this.state.player.grid}
          playersCount={this.state.game.count}
          quitGame={this.quitGame}
        />
        <Grid
          playerNumbers={this.state.player.numbers}
          gridNumbers={this.state.player.grid}
          finish={this.state.game.winner || this.state.player.finish}
          gameNumbers={this.state.game.numbers}
          toggleNumber={this.toggleNumber}
        />
        {!this.state.game.key &&
          !this.state.admin && (
          <PlayerForm
            formOpened={this.state.formOpened}
            gameName={this.state.game.name}
            playerName={this.state.player.name}
            changeGameName={this.changeGameName}
            changePlayerName={this.changePlayerName}
            changeFormOpened={this.changeFormOpened}
            joinGame={this.joinGame}
            createGame={this.createGame}
          />
        )}
        {this.state.admin && (
          <Admin
            drawNumber={this.drawNumber}
            gameNumbers={this.state.game.numbers}
            players={this.state.game.players}
            winner={this.state.game.winner}
          />
        )}
        {this.state.message && (
          <Message
            message={this.state.message}
            removeMessage={this.removeMessage}
          />
        )}
      </div>
    )
  }
}

export default App;
