import React from 'react';
import AdminPlayer from './AdminPlayer';

const styles = {
  container: {
    marginTop: 20,
  },
  title: {
    fontWeight: 'bold',
    paddingRight: 20,
    fontSize: '1.5em',
    margin: '30px 0',
  },
  grid: {
    margin: '30px auto',
    maxWidth: 400,
    display: 'grid',
    gridTemplateColumns: 'repeat(15, 1fr)',
  },
  number: {
    display: 'flex',
    width: 20,
    height: 20,
    borderRadius: 10,
    background: '#EEE',
    color: '#AAA',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '0.75em',
    marginBottom: 5,
  },
  drawn: {
    background: '#999',
    color: '#EEE',
  },
  draw: {
    background: '#EFE',
    padding: '3px 10px',
    border: '2px solid #ADA',
    color: '#6C6',
    borderRadius: 3,
    cursor: 'pointer',
  },
  list: {
  },
};

const allNumbers = [...Array(75).keys()].map(number => number + 1);

class Admin extends React.Component {
  render() {
    return (
      <div style={styles.container}>
        <div>
          <span style={styles.title}>Numéros</span>
          {!this.props.winner && this.props.gameNumbers.length < 75 && (
            <span
              style={styles.draw}
              onClick={this.props.drawNumber}
            >Tirer un numéro</span>
          )}
        </div>
        <div style={styles.grid}>
          {allNumbers.map(number => (
            <div style={{
              ...styles.number,
              ...(this.props.gameNumbers.includes(number) && styles.drawn)
            }}>
              {number}
            </div>
          ))}
        </div>

        <div style={styles.title}>{this.props.players.length} participant{this.props.players.length > 1 && 's'}</div>
        <div style={styles.list}>
          {this.props.players.map(player => (
            <AdminPlayer
              player={player}
              winner={this.props.winner === player.key}
              gameNumbers={this.props.gameNumbers}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default Admin;
