import React from 'react';

const softColors = ['#00A', '#FA0', '#0A0', '#F00', '#A0A'];
const boldColors = ['#EEF', '#FED', '#EFE', '#FEE', '#FEF'];
const styles = {
  container: {
  },
  circle: {
    margin: '20px auto',
    borderRadius: '50%',
    background: '#EEE',
    color: '#DDD',
    height: 70,
    width: 70,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '250%',
  },
  activated: (index) => ({
    background: softColors[index],
    color: boldColors[index],
  }),
};

class HeaderCase extends React.Component {
  render() {
    const columnNumbers = [...this.props.gridNumbers].splice(5 * this.props.index, 5);
    const activated = columnNumbers.every(number => number === '_' || this.props.playerNumbers.includes(number));

    return (
      <div style={styles.container}>
        <div style={{
          ...styles.circle,
          ...(activated && styles.activated(this.props.index))
        }}>
          {this.props.letter}
        </div>
      </div>
    );
  }
}

export default HeaderCase;
