import React from 'react';
import Case from './Case';
import HeaderCase from './HeaderCase';

const bingo = 'BINGO'.split('');
const styles = {
  container: {
  },
  wrapper: {
    display: 'grid',
    gridTemplate: 'repeat(5, 1fr) / repeat(5, 1fr)',
    gridAutoFlow: 'column',
  },
  header: {

  },
  header: {
    display: 'grid',
    gridTemplateColumns: 'repeat(5, 1fr)',
  },
};

class Grid extends React.Component {
  render() {
    return (
      <div style={styles.container}>
        <div style={styles.header}>
          {bingo.map((letter, index) => (
            <HeaderCase
              index={index}
              letter={letter}
              playerNumbers={this.props.playerNumbers}
              gridNumbers={this.props.gridNumbers}
            />
          ))}
        </div>
        <div style={styles.wrapper}>
          {this.props.gridNumbers.map(number => (
            <Case
              number={number}
              playerNumbers={this.props.playerNumbers}
              finish={this.props.finish}
              gameNumbers={this.props.gameNumbers}
              toggleNumber={this.props.toggleNumber}
            />)
          )}
        </div>
      </div>
    );
  }
}

export default Grid;
