import React from 'react';

const styles = {
  container: {
    position: 'absolute',
    top: 0,
    right: 0,
    left: 0,
    bottom: 0,
    minHeight: '100vw',
  },
  background: {
    position: 'absolute',
    height: '100%',
    width: '100%',
    background: '#FFF',
    opacity: 0.75,
  },
  wrapper: {
    position: 'sticky',
    margin: '170px auto 50px',
    maxWidth: '75%',
    width: 400,
    background: '#EEE',
    border: '2px solid #666',
    color: '#666',
    padding: 20,
    fontSize: '1.25em',
    borderRadius: 10,
  },
  actions: {
    marginTop: 20,
    textAlign: 'center',
  },
  button: {
    display: 'inline-block',
    margin: '10px 20px',
    background: '#E6E6E6',
    color: '#999',
    padding: '5px 20px',
    borderRadius: 10,
    cursor: 'pointer',
  },
  confirm: {
    background: '#666',
    color: '#CCC',
  },
  cancel: {
  },
};

class Message extends React.Component {
  componentDidUpdate() {
    if (this.props.message.timeout) {
      setTimeout(() => {
        this.props.removeMessage();
      }, this.props.message.timeout);
    }
  }

  onClick = (ok) => (() => this.props.message.callback(ok));

  getContent = () => {
    const content = this.props.message.content;
    if (typeof content === 'function') {
      return content(this.props.message.data);
    }

    return content;
  };

  render() {
    return (
      <div style={styles.container}>
        <div style={styles.background}></div>
        <div style={styles.wrapper}>
          <div style={styles.content}>{this.getContent()}</div>
          {(this.props.message.confirm || this.props.message.cancel) && (
            <div style={styles.actions}>
              {this.props.message.confirm && (
                <div
                  style={{
                    ...styles.button,
                    ...styles.confirm
                  }}
                  onClick={this.onClick(true)}
                >{this.props.message.confirm}</div>
              )}
              {this.props.message.cancel && (
                <div
                  style={{
                    ...styles.button,
                    ...styles.cancel
                  }}
                  onClick={this.onClick(false)}
                >{this.props.message.cancel}</div>
              )}
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Message;
