import React from 'react';

const styles = {
  free: {
    color: '#666',
    fontSize: '1em',
    background: '#DDD',
    borderColor: '#666',
  },
  common: {
    margin: 10,
    borderRadius: 4,
    height: 60,
    border: '1px solid #DDD',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: '1.5em',
    color: '#999',
  },
  hover: {
    color: '#666',
    border: '1px solid #AAA',
    background: '#EEE',
    cursor: 'pointer',
  },
  selected: {
    color: '#EA3',
    border: '1px solid #EA3',
    background: '#FEA',
  },
  hoverSelected: {
    color: '#C92',
    border: '1px solid #C92',
    background: '#ED9',
  },
  checked: {
    border: '1px solid #6C6',
    background: '#EFE',
    color: '#6C6',
  },
  mistake: {
    border: '1px solid #C66',
    background: '#FEE',
    color: '#C66',
  },
};

class Case extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hover: false,
    };
  }

  mouseToggle = () => {
    this.setState({hover: !this.state.hover});
  };

  toggle = () => {
    this.props.toggleNumber(this.props.number);
  };

  render() {
    if (this.props.number === '_') {
      return (
        <div style={{...styles.common, ...styles.free}}>
          FREE
        </div>
      );
    }

    const selected = this.props.playerNumbers.indexOf(this.props.number) >= 0;
    const mistake = !this.props.gameNumbers.includes(this.props.number);
    return (
      <div
        style={{
          ...styles.common,
          ...(selected && styles.selected),
          ...(selected && this.props.finish && !mistake && styles.checked),
          ...(selected && this.props.finish && mistake && styles.mistake),
          ...(!this.props.finish && this.state.hover && styles.hover),
          ...(!this.props.finish && this.state.hover && selected && styles.hoverSelected),
        }}
        onMouseEnter={this.mouseToggle}
        onMouseLeave={this.mouseToggle}
        onClick={!this.props.finish && this.toggle}
      >
        {this.props.number}
      </div>
    );
  }
}

export default Case;
