function generateNumbers(withFree = true) {
  return [
    ...generateBlockNumbers(1, 15, 5),
    ...generateBlockNumbers(16, 30, 5),
    ...generateBlockNumbersWithFree(31, 45, 5, withFree, 2),
    ...generateBlockNumbers(46, 60, 5),
    ...generateBlockNumbers(61, 75, 5),
  ];
}

function generateBlockNumbers(from, to, count = 5) {
  const numbers = [...Array(to - from + 1).keys()]
    .sort(() => 0.5 - Math.random())
    .map(x => x + from)
    .slice(0, count);
  return numbers;
}

function generateBlockNumbersWithFree(from, to, count, withFree, freePosition) {
  const numbers = generateBlockNumbers(from, to, count);
  if (withFree) {
    numbers[freePosition] = '_';
  }

  return numbers;
}

export { generateNumbers, generateBlockNumbers };
