import React from 'react';

const styles = {
  number: {
    display: 'flex',
    width: 20,
    height: 20,
    borderRadius: 10,
    background: '#EEE',
    color: '#AAA',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '0.75em',
    marginBottom: 5,
  },
  checked: {
    background: '#EFE',
    color: '#6C6',
  },
  mistake: {
    background: '#FEE',
    color: '#C66',
  },
  oversight: {
    background: '#FED',
    color: '#FA0',
  },
  grid: {
    maxWidth: 150,
    display: 'grid',
    gridTemplate: 'repeat(5, 1fr) / repeat(5, 1fr)',
    gridAutoFlow: 'column',
  },
};

class AdminPlayerGrid extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  styleNumber = (number) => {
    if (number === '_') {
      return null;
    }

    if (this.props.gameNumbers.includes(number)) {
      if (this.props.playerNumbers.includes(number)) {
        return styles.checked;
      } else {
        return styles.oversight;
      }
    } else if (this.props.playerNumbers.includes(number)) {
      return styles.mistake;
    }
  };
/*
<div style={{
  ...styles.number,
}}>*/
  render() {
    return (
      <div style={styles.grid}>
        {this.props.gridNumbers.map(number => (
          <div style={{
            ...styles.number,
            ...this.styleNumber(number),
          }}>{number === '_' ? '' : number}</div>
        ))}
      </div>
    );
  }
}

export default AdminPlayerGrid;
