import React from 'react';
import hash from 'object-hash';

const styles = {
  container: {
    textAlign: 'center',
  },
  menuOpened: {
    background: '#EEE',
    border: '2px solid #666',
    color: '#666',
    padding: 20,
    width: '75%',
    maxWidth: '250px',
    margin: '20px auto',
    fontSize: '1.5em',
    borderRadius: 10,
  },
  header: {
    cursor: 'pointer',
  },
  menuClosed: {
    textDecoration: 'underline',
    color: '#CCC',
    marginTop: 40,
    cursor: 'pointer',
  },
  form: {
    borderTop: '1px solid #CCC',
    marginTop: 20,
  },
  label: {
    display: 'flex',
    width: '100%',
    textAlign: 'right',
    marginTop: 20,
    alignItems: 'center',
  },
  input: {
    flex: 1,
    width: '100%',
    outline: 'none',
    fontSize: '0.75em',
    padding: '3px 10px',
    border: 0,
    background: '#DDD',
    borderBottom: '1px solid #BBB',
    borderRadius: 0,
    color: '#333',
  },
  field: {
    flex: '0 70px',
    marginRight: 20,
    fontSize: '0.75em',
    boxSizing: 'border-box',
  },
  go: {
    display: 'inline-block',
    margin: '20px auto 0',
    background: '#E6E6E6',
    color: '#CCC',
    padding: '5px 20px',
    borderRadius: 10,
  },
  goActivated: {
    background: '#666',
  }
};

class PlayerForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pass: '',
    };
  }

  toggleForm = (form) => (() => {
    let futureForm = null;
    if (this.props.formOpened !== form) {
      futureForm = form;
    }
    this.setState({pass: ''});
    this.props.changeFormOpened(futureForm);
  });

  changeValue = (field) => ((event) => {
    if (field === 'pass') {
      this.setState({pass: event.target.value});
    } else if (field === 'game') {
      this.props.changeGameName(event.target.value);
    } else if (field === 'player') {
      this.props.changePlayerName(event.target.value);
    }
  });

  createGame = () => {
    this.props.createGame(this.state.pass);
  };

  render() {
    return (
      <div style={styles.container}>
        {this.props.formOpened !== 'create' && (
          <div style={styles.menuOpened}>
            <div
              style={styles.header}
              onClick={this.toggleForm('join')}
            >
              <b>Joindre</b> une partie
            </div>
            {this.props.formOpened === 'join' && (
              <div style={styles.form}>
                <label
                  htmlFor="gameName"
                  style={styles.label}
                >
                  <div style={styles.field}>Partie</div>
                  <input
                    style={styles.input}
                    value={this.props.gameName}
                    id="gameName"
                    onChange={this.changeValue('game')}
                  />
                </label>

                <label
                  htmlFor="playerName"
                  style={styles.label}
                >
                  <div style={styles.field}>Prénom</div>
                  <input
                    style={styles.input}
                    value={this.props.playerName}
                    id="playerName"
                    onChange={this.changeValue('player')}
                  />
                </label>

                <div
                  style={{
                    ...styles.go,
                    ...(this.props.gameName && this.props.playerName && styles.goActivated)
                  }}
                  onClick={this.props.joinGame}
                >
                  Go !
                </div>
              </div>
            )}
          </div>
        )}
        {this.props.formOpened !== 'join' && (
          <div style={this.props.formOpened === 'create' ? styles.menuOpened : styles.menuClosed}>
            <div
              style={styles.header}
              onClick={this.toggleForm('create')}
            >
              <b>Administrer</b> une partie
            </div>
            {this.props.formOpened === 'create' && (
              <div style={styles.form}>
                <label
                  htmlFor="gameName"
                  style={styles.label}
                >
                  <div style={styles.field}>Partie</div>
                  <input
                    style={styles.input}
                    value={this.props.gameName}
                    id="gameName"
                    onChange={this.changeValue('game')}
                  />
                </label>

                <label
                  htmlFor="gamePass"
                  style={styles.label}
                >
                  <div style={styles.field}>Clef</div>
                  <input
                    type="password"
                    style={styles.input}
                    value={this.state.pass}
                    id="gamePass"
                    onChange={this.changeValue('pass')}
                  />
                </label>

                <div
                  style={{
                    ...styles.go,
                    ...(this.props.gameName && this.state.pass && styles.goActivated)
                  }}
                  onClick={this.createGame}
                >
                  Go !
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default PlayerForm;
