import React from 'react';

const styles = {
  container: {
    display: 'flex',
    width: '100%',
    alignItems: 'center',
    height: 30,
  },
  count: {
    flex: 1,
    color: '#999',
  },
  game: {
    padding: '0 10px 0 20px',
    fontWeight: 'bold',
  },
  player: {
    paddingRight: 10,
  },
  quit: {
    paddingRight: 20,
    background: '#FEE',
    padding: '3px 10px',
    border: '2px solid #FCC',
    color: '#F66',
    borderRadius: 3,
    cursor: 'pointer',
  }
};

class GameInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  // TODO : Supprimer duplication
  render() {
    if (this.props.admin) {
      return (
        <div style={styles.container}>
          <span style={styles.game}>{this.props.gameName}</span>
          <span style={styles.count}>ADMINISTRATION</span>
          <span
            style={styles.quit}
            onClick={this.props.quitGame}
          >
            Quitter
          </span>
        </div>
      );
    } else if (this.props.gridNumbers.length === 0) {
      return (
        <div style={styles.container}></div>
      );
    }

    return (
      <div style={styles.container}>
        <span style={styles.game}>{this.props.gameName}</span>
        <span style={styles.count}>
          {this.props.playersCount} joueur{this.props.playersCount > 1 && 's'}
        </span>
        <span style={styles.player}>{this.props.playerName}</span>
        <span
          style={styles.quit}
          onClick={this.props.quitGame}
        >
          Quitter
        </span>
      </div>
    );
  }
}

export default GameInfo;
