import React from 'react';
import AdminPlayerGrid from './AdminPlayerGrid';

const styles = {
  container: {
    display: 'inline-block',
    width: 140,
    padding: 5,
    margin: 5,
    background: '#F9F9F9',
    borderRadius: 5,
  },
  winner: {
    background: '#FC0',
  },
  title: {
    textAlign: 'center',
    marginBottom: 10,
  }
};

class AdminPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    return (
      <div style={{...styles.container, ...(this.props.winner && styles.winner)}}>
        <div style={styles.title}>
          {this.props.player.name}
          {this.props.player.auto && ' (auto)'}
        </div>
        <AdminPlayerGrid
          playerNumbers={this.props.player.numbers}
          gridNumbers={this.props.player.grid}
          gameNumbers={this.props.gameNumbers}
        />
      </div>
    );
  }
}

export default AdminPlayer;
